<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">新建、修改公告</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="formcontent">
			<form>
				<view class="cu-form-group margin-top">
					<view class="title">公告标题</view>
					<input placeholder="请输入公告标题" v-model="form.title"></input>
				</view>
				<view class="cu-form-group margin-top">
					<view class="title">公告类型</view>
					<picker @change="PickerChange" :value="index" :range="picker">
						<view class="picker">
							{{index>-1?picker[index]:'请选择'}}
						</view>
					</picker>
				</view>
				<view class="cu-form-group">
					<view class="title">公告内容</view>
					<input placeholder="请输入公告内容" v-model="form.content"></input>
				</view>
				<view class="cu-form-group">
					<view class="title">发布选择</view>
					<picker mode="date" :value="form.datetime" start="2021-09-01" end="2025-09-01" @change="DateChange">
						<view class="picker">
							{{form.datetime}}
						</view>
					</picker>
				</view>
			</form>
			<view class="margin-top submitbut">
				<button class="cu-btn block bg-blue margin-tb-sm lg" @click="submit">提交</button>
			</view>
		</view>
	</view>
</template>

<script>
	import request from '../../api/api.js'
	import urls from '../../api/request.js'
	import {
		mobileNoTest
	} from '../../utils/rules.js'
	import {
		deepCopy
	} from '../../utils/expressfun.js'
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		components: {
			homecom
		},
		onLoad(option) {
			console.log(option)
			this.id = option.id
			if (this.id != 'add') {
				this.form.title = option.title
				this.form.content = option.content
				this.form.datetime = option.datetime
				this.index = option.typestr
			}

		},
		mounted() {
			this.init()
		},
		data() {
			return {
				index: -1,
				picker: ['违规通告', '闭关通知'],
				id: '',
				form: {
					datetime:'2018-12-25',
					title: '',
					content: '',
				},

			}
		},
		methods: {
			init() {},
			PickerChange(e) {
				this.index = e.detail.value
			},
			DateChange(e) {
				this.form.datetime = e.detail.value
			},
			async submit() {
				if (this.id == 'add') {
					let obj = deepCopy(this.form)
					obj.typestr = this.index
					let resdata = await new this.Request(this.Urls.m().announcementinsert, obj).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../projectlist/projectlist`
							});
						}, 1500)
					}
				} else {
					let obj = deepCopy(this.form)
					obj.id = this.id
					obj.typestr = this.index
					let resdata = await new this.Request(this.Urls.m().announcementedit, obj).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../projectlist/projectlist`
							});
						}, 1500)
					}
				}

			},
		}
	}
</script>

<style scoped>
	.submitbut {
		padding: 15px;
	}

	.titlesub {
		margin-right: 10px;
	}
</style>
